.habilidades-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height:100vh;
    padding:60px;
    background-color: #343233;

}


.habilidades-container h2{
    
    font-family: var(--montserrat);
    line-height: 2.5rem;
    color: #efea;
    font-size: 1.8rem;   
    margin-bottom: 2rem;
}


.habilidades-container h3{
    margin-top: 0;
    margin-bottom: 0.25em;
    font-weight: 500;
    font-size: 1.2em;
    color:#9fd4ca;
    margin-left: 3rem;
}

.habilidades-container ul li{

    list-style: none;
    text-decoration: none;
    margin-left: 3rem;
    font-weight: 300;
    font-size: 1rem;
    margin-top: 10px;
    color: navajowhite;
    font-family: 'Lato', sans-serif;
    line-height: 1.5;
    text-rendering: optimizeLegibility;


}

#habilidades ul li {
    margin-left: 3rem;
    font-weight: 300;
    font-size: 1rem;
    color: navajowhite;
    font-family: 'Lato', sans-serif;
    line-height: 1.5;
    text-rendering: optimizeLegibility;
}

.img-habilidades{ 
    background-image: url(../../img/fondo-habilidades.png);
    background-repeat:no-repeat;
    background-size: cover;
    height:500px;
    width: 500px;
    margin-top: 100px;
}

.curriculum {
    background-color: #F8AF75;
    padding: 0.8rem;
    margin-left: 0.2rem;
    font-weight: bold;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;
    cursor: pointer;
    width: 12rem;
    transition: all 400ms ease;
}

.curriculum a {
    display: flex;
    justify-content: center;
    align-items: center;
}

a {
    color: white;
    background-color: transparent;
    text-decoration: none;
}
a:hover {
    color: #F8AF75;
    background-color: transparent;
    text-decoration: none;
}

.curriculum:hover {
    
    background-color:white;


}

